@font-face {
    font-family: 'Poppins-Medium';
    font-style: Medium;
    src: url('./fonts/Poppins/Poppins-Medium.ttf');
}
@font-face {
    font-family: 'Poppins-Light';
    font-style: Light;
    src: url('./fonts/Poppins/Poppins-Light.ttf');
}
@font-face {
    font-family: 'Poppins';
    font-style: normal;
    src: url('./fonts/Poppins/Poppins-Regular.ttf');
}

@mixin poppins10{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 24px;
}

