@mixin windowMax2048p{
    @media (min-width: 1921px){
        @content;
    }
}

@mixin windowMax1920p{
    @media (min-width: 1441px) and (max-width: 1920px){
        @content;
    }
}

@mixin windowMax1440p{
    @media (min-width: 952px) and (max-width: 1440px){
        @content;
    }
}

