@mixin windowMax1280p{
    @media (min-width: 952px) and (max-width: 1280px){
        @content;
    }
 }

@mixin mobileMax952p{
    @media (min-width: 0px) and (max-width: 952px){
        @content;
    }
}

@mixin tablet768-952p{
    @media (min-width: 768px) and (max-width: 952px) {
        @content;
    }
}