.nav-wrap-4k{
    display: flex;
    justify-content: center;
}
.nav{
    position: absolute;
    display: flex; 
    align-items: center; 
    justify-content: space-between;
    width: calc(100% - 80px);
    padding: 32px 40px 0px 40px;
    z-index: 9999;
    @include mobileMax952p{
        padding: 32px 32px 0px 32px;
    }
    @include windowMax2048p{
        max-width: 1920px;
    }
    &__logo{
        img{
            width: 177px;
            height: 18px;
            @include windowMax1440p {
                width: 141px;
                height: 14px; 
            }
        }
    }
    &__burger{
        @include mobileMax952p{
            &::before{
                content: "";
                height: 2px;
                background-color: white;
                width: 18px;
                display: block;
            }
            &::after{
                content: "";
                height: 2px;
                background-color: white;
                width: 18px;
                margin-top: 4px;
                display: block;
            }
        }
    }
    &__menu{
        display: flex;
        align-items: center;
        gap: 48px;
        @include mobileMax952p{
            display: none;
        }
        &__item{
            @include poppins14_500();
            color: var(--white-color);
            text-align: center;
            letter-spacing: 0.08em;
            font-feature-settings: "liga" off;
            text-transform: uppercase;
            cursor: pointer;
            @include windowMax1440p {
                @include poppins12_500();
                letter-spacing: 0.1em;
            }
            @include windowMax1280p{
                @include poppins10_500();
            }
            &::after{
                content: "";
                height: 2px;
                background-color: white;
                width: 0px;
                display: inherit;
                transition: width 0.3s ease-out;
            }
            &:hover{
                &::after{
                    width: 100%;
                }
            }
        }
        &__button{
            background-color: var(--iris-color);
            border-radius: 600px;
            cursor: pointer;
            transition: background-color 0.3s ease-out;
            p{
                padding: 14px 32px 14px 32px;
                @include poppins14_500();
                color: var(--white-color);
                text-transform: uppercase;
                @include windowMax1440p {
                    padding: 12px 28px 12px 28px;
                    @include poppins12_500();
                    letter-spacing: 0.08em;
                }
                @include windowMax1280p{
                    @include poppins10_500();
                    letter-spacing: 0.1em;
                }
            }
            &:hover{
                background-color: var(--iris-color-hover);
            }
        }
    }
}