.bottom{
    height: 1605px;
    position: relative;
    overflow: hidden;
    @include windowMax1440p {
        height: 1201px;
    }
    @include windowMax1280p{
        height: 1078px;
    }
    @include mobileMax952p{
        height: 1313px;
    }
    &__bg{
        position: absolute;
        width: 100%;
        height: 100%;
        background-image: url("./images/bg/bg-bottom.png");
        background-size: auto;
        @include windowMax2048p {
            background-size: cover;
        }
        @include mobileMax952p{
            height: 1313px;
            overflow: hidden;
        }
        &__overlay{
            width: 100%;
            height: 100%;
            position: absolute;
            background: var(--iris-color);
            mix-blend-mode: overlay;
            margin-top: 40px;
        }
        &__gradient{
            height: 824px;
            width: 100%;
            background: linear-gradient(180deg, #18183C 0%, rgba(24, 24, 60, 0) 100%);
            position: absolute;
            margin-top: 40px;
        }
        img{
            width: 100%;
        }
    }
    &__circleWrap{
        position: absolute;
        width: 100%;
        height: 100%;      
        display: flex;
        justify-content: center;
        bottom: 0;
        &__circle{
            width: calc(100vw - 212px);
            height: calc(100vw - 212px);
            opacity: 0.1;
            border: 2px solid var(--white-color);
            //margin-top: calc(100vw * 0.15520);
            border-radius: 100%;
            margin-left: 106px;
            margin-right: 106px;
            transform: translateY(22.4%);
            @include windowMax2048p{
                transform: translateY(10%);
            }
            @include windowMax1440p {
                width: calc(100vw - 160px);
                height: calc(100vw - 160px);
                margin-left: 80px;
                margin-right: 80px;
            }
            
            @include windowMax1280p{
                height: 1200px;
                width: 1200px;
                flex-shrink: 0;
                /*
                width: calc(100vw - 148px);
                height: calc(100vw - 148px);
                margin-left: 74px;
                margin-right: 74px;*/
            }
            @include mobileMax952p{ 
                height: 1200px;
                width: 1200px;
                margin-top: 16px;
                margin-bottom: 97px;
                flex-shrink: 0;
                transform: none;
            }
        }
    }
    &__content{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: end;
        height: 100%;
        &__headline{
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 56px;
            z-index: 1;
            @include windowMax1440p {
                gap: 40px;
            }
            @include windowMax1280p{
                gap: 32px;
            }
            @include mobileMax952p{
                gap: 32px;
            }
            img{
                width: 296px;
                height: 30px;
                @include windowMax1440p {
                    width: 218px;
                    height: 22px; 
                }
                @include windowMax1280p{
                    width: 174px;
                    height: 17px;
                }
                @include mobileMax952p{
                    width: 156px;
                    height: 16px;
                }
            }
            p{
                @include poppins56();
                letter-spacing: 0.02em;
                color: var(--white-color);
                max-width: 634px;
                text-align: center;
                @include windowMax1440p {
                    @include poppins40(); 
                }
                @include windowMax1280p{
                    @include poppins32();
                }
                @include mobileMax952p{
                    @include poppins32();
                    max-width: 316px;
                }
            }
        }
        &__contacts{
            padding-top: 200px;
            display: flex;
            justify-content: space-between;
            gap: 181px;
            padding-left: 72px;
            padding-right: 72px;
            @include windowMax1440p {
                padding-top: 140px;
                padding-right: 0px;
                padding-left: 0px;
                max-width: 1100px;  
            }
            @include windowMax1280p{
                padding-top: 132px;
                gap: 167px;
                max-width: 960px;  
            }
            @include mobileMax952p{
                padding-top: 64px;
                flex-direction: column;
                align-items: center;
                gap: 48px;
            }
            h4{
                @include poppins16();
                letter-spacing: 0.02em;
                color: var(--white-color);
                padding-bottom: 16px;
                z-index: 1;
                @include windowMax1440p {
                    @include poppins14();
                }
                @include mobileMax952p{
                    text-align: center;
                    @include poppins14();
                }
            }
            p{
                @include poppins16();
                letter-spacing: 0.02em;
                color: var(--white-color);
                opacity: 0.7;
                line-height: 34px;
                @include windowMax1440p {
                    @include poppins14();
                }
                @include windowMax1280p{
                    @include poppins12();
                }
                @include mobileMax952p{
                    @include poppins14();
                    text-align: center;
                }
            }
            a{
                &:link, &:visited,&:active{
                    text-decoration: none;
                    color: var(--white-color);
                }
            }
            &__item{
                display: flex;
                flex-direction: column;
            }
            &__item{
                &-underline{
                    text-decoration: underline;
                }
            }
        }
        &__copyright{
            padding-top: 184px;
            padding-bottom: 40px;
            @include mobileMax952p{
                padding-top: 83px;
                padding-bottom: 24px;
            }
            p{
                @include poppins16_300();
                letter-spacing: 0.04em;
                color: var(--white-color);
                opacity: 0.7;
                @include windowMax1440p {
                    @include poppins14_300();
                }
                @include mobileMax952p{
                    @include poppins14_300();
                    text-align: center;
                }
            }
        }
    }
}