.text{
    overflow: hidden;
    &__content{
        padding-top: 260px;
        @include windowMax1440p {
            padding-top: 200px;
        }
        @include windowMax1280p{
            padding-top: 160px;
        }
        @include mobileMax952p{
            padding-top: 120px;
        }
        &__divider{
            display: flex;
            align-items: center;
            gap: 32px;
            p{
                @include poppins16();
                letter-spacing: 0.1em;
                color: var(--white-color);
                opacity: 0.4;
                text-transform: uppercase;
                @include windowMax1440p {
                    @include poppins12();
                }
                @include windowMax1280p{
                    @include poppins10();
                }
                @include mobileMax952p{
                    @include poppins14();
                }
            }
            &__line{
                height: 1px;
                width: 300%;
                background-color: var(--white-color);
                opacity: 0.16;
                flex-shrink: 0;
            }
        }
        h2{
            @include poppins56();
            padding-top: 48px;
            letter-spacing: 0.02em;
            color: var(--white-color);
            max-width: 850px;
            @include windowMax1440p {
                @include poppins40();
                max-width: 569px;
            }
            @include windowMax1280p{
                @include poppins32();
            }
            @include mobileMax952p{
                @include poppins28();
                padding-top: 32px;
            }
        }
        &__block{
            @include poppins16_300();
            letter-spacing: 0.04em;
            color: var(--white-color);
            opacity: 0.8;
            padding-top: 56px;
            padding-left: 72px;
            max-width: 912px;
            @include windowMax1440p {
                @include poppins14_300();
                padding-left: 77px;
                max-width: 744px;
            }
            @include windowMax1280p{
                @include poppins12_300();
                padding-left: 58px;
                max-width: 738px;
            }
            @include mobileMax952p{
                @include poppins14_300();
                padding-left: 0px;
            }
        }
        &__numbers{
            display: flex;
            gap: 215px;
            padding-top: 148px;
            padding-left: 72px;
            @include windowMax1440p {
                padding-top: 96px;
                padding-left: 77px;
                gap: 164px;
            }
            @include windowMax1280p{
                padding-top: 88px;
                padding-left: 58px;
                gap: 151px;
            }
            @include mobileMax952p{
                padding-top: 64px;
                padding-left: 0px;
                padding-bottom: 104px;
                gap: 73px;
                flex-wrap: wrap;
                justify-content: center;
            }
            &__item{
                display: flex;
                flex-direction: column;
                h3{
                    @include poppins56_500();
                    letter-spacing: 0.04em;
                    color: var(--white-color);
                    @include windowMax1440p {
                        @include poppins40_500();
                        letter-spacing: 0.02em;
                    }
                    @include mobileMax952p{
                        @include poppins32_500();
                        letter-spacing: 0.02em;
                    }
                }
                p{
                    @include poppins16_300();
                    letter-spacing: 0.04em;
                    opacity: 0.8;
                    color: var(--white-color);
                    padding-top: 24px;
                    @include windowMax1440p {
                        @include poppins14_300();
                        padding-top: 8px;
                    }
                    @include windowMax1280p{
                        @include poppins12_300();
                        
                    }
                    @include mobileMax952p{
                        @include poppins14_300();
                        padding-top: 8px;
                        width: 117px;
                    }
                }   
            }
        }
    }
}