.hero{
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow-x: hidden;
    &__bg{
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: visible;
        overflow-x: hidden;
        z-index: -1;
        @include mobileMax952p{
            display: flex;
            justify-content: center;
        }
        img{
            width: 100%;
            @include mobileMax952p{
                width: unset;
                transform: scale(1.5) translateY(10%);
            }
        }
    }
    &__content{
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        &__circle{
            width: calc(100vh - 120px);
            height: calc(100vh - 120px);
            margin: 60px 0px 60px 0px;
            flex-shrink: 0;
            border-radius: 100%;
            background: #05051bf1;
            display: flex;
            justify-content: center;
            align-items: center;
            @include mobileMax952p{
                width: calc(100vh - 252px);
                height: calc(100vh - 252px);
                margin: 126px 0px 126px 0px;
            }
            &__ring{
                margin: 9.375%;
                height: calc(100% - 2 * 9.375%);
                width: calc(100% - 2 * 9.375%);
                flex-shrink: 0;
                border-radius: 100%;
                border: 2px solid var(--iris-color);
                display: flex;
                justify-content: center;
                align-items: center;
                &__text{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    @include mobileMax952p{
                        max-width: 301px;
                    }
                    h1{
                        @include poppins56();
                        max-width: 634px;
                        text-align: center;
                        color: var(--white-color);
                        padding-bottom: 40px;
                        letter-spacing: 0.02em;
                        @include windowMax1440p {
                            @include poppins40();
                        }
                        @include windowMax1280p{
                            @include poppins32();
                        }
                        @include mobileMax952p{
                            @include poppins32();
                        }
                    }
                    &__action{
                        display: flex;
                        gap: 16px;
                        cursor: pointer;
                        z-index: 1;
                        p{
                            @include poppins14_500();
                            color: var(--white-color); 
                            letter-spacing: 0.1em;
                            font-feature-settings: 'liga' off;
                            text-transform: uppercase;
                            @include windowMax1440p {
                                @include poppins12_500();
                            }
                            @include windowMax1280p{
                                @include poppins10_500();
                            }
                            @include mobileMax952p{
                                @include poppins12_500();
                            }
                            /*
                            &::after{
                                content: "";
                                height: 2px;
                                background-color: white;
                                width: 100%;
                                left:50%;
                                display: inherit;
                                transition: transform 0.3s ease-out;
                                transform: scaleX(0);
                            }*/
                        }
                        &__button{
                            background-color: var(--iris-color);
                            transition: background-color 0.3s ease-out;
                            width: 28px;
                            height: 28px;
                            border-radius: 60px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            @include windowMax1280p{
                                width: 24px;
                                height: 24px;
                            }
                        }
                        &:hover &__button{
                            background-color: var(--iris-color-hover);
                        }
                        /*
                        &:hover p{
                            &::after{
                                transform: scaleX(1);
                            }
                        }*/
                    }
                }
            }
        }
    }
    &__shape{
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        flex-shrink: 0;
        top: 0px;
        z-index: 0;
        transform: scale(0.9);
        @include mobileMax952p{
            transform: scale(0.68);
        }
        svg{
            transform-origin: 313px 524px;
            translate: calc(100vw / 2 - 313px) calc(100vh / 2 - 524px);
            flex-shrink: 0;
        }
    }
}