.white{
    border-radius: 32px;
    background-color: var(--white-color);
    overflow: hidden;
    &__firstText{
        padding-top: 260px;
        @include windowMax1440p {
            padding-top: 180px; 
        }
        @include windowMax1280p{
            padding-top: 160px;
        }
        @include mobileMax952p{
            padding-top: 96px;
        }
        &__heading-wrap{
            max-width: 1080px;
            @include windowMax1440p {
                max-width: 809px;
            }
            @include windowMax1280p{
                max-width: 678px;
            }
            h2{
                @include poppins52();
                letter-spacing: 0.02em;
                color: var(--iris-color);
                display: inline;
                @include windowMax1440p {
                    @include poppins48();
                }
                @include windowMax1280p{
                    @include poppins32();
                    line-height: 54px;
                }
                @include mobileMax952p{
                    @include poppins28();
                }
            }
            img{
                display: inline;
                vertical-align: bottom;
                @include windowMax1440p {
                    width: 96px;
                    height: 57px;
                }
                @include windowMax1280p{
                    width: 80px;
                    height: 48px;
                }
                @include mobileMax952p{
                    width: 72px;
                    height: 42px;
                }
            }
        }
        p{
            @include poppins16_300();
            letter-spacing: 0.04em;
            max-width: 910px;
            padding-top: 40px;
            color: var(--space-cadet-color);
            @include windowMax1440p {
                @include poppins14_300();
                max-width: 809px;   
            }
            @include windowMax1280p{
                @include poppins12_300();
                padding-top: 32px;
                max-width: 678px;
            }
            @include mobileMax952p{
                @include poppins14_300();
                padding-top: 24px;
            }
        }
    }
    &__pictures{
        display: flex;
        gap: 24px;
        padding: 200px 36px 0px 36px;
        @include windowMax1440p {
            padding: 140px 24px 0px 24px;   
        }
        @include windowMax1280p{
            padding: 120px 16px 0px 16px;  
        }
        @include mobileMax952p{
            padding: 72px 24px 0px 24px;
        }
        &__item{
            position: relative;
            width: 100%;
            height: 100%;
            @include mobileMax952p{
                flex-shrink: 0.13;
            }
            &__overlay{
                cursor: pointer;
                position: absolute;
                background-color: var(--iris-color);
                mix-blend-mode: overlay;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: opacity 0.3s ease-out;
            }
            p{
                cursor: pointer;
                @include poppins16_500();
                letter-spacing: 0.1em;
                text-transform: uppercase;
                font-feature-settings: 'liga' off;
                position: absolute;
                bottom: 32px;
                left: 40px;
                color: #0000FF;
                opacity: 0;
                transition: opacity 0.3s ease-out;
            }
            img{
                width: 100%;
                height: 100%;
            }
            &:hover &__overlay{
                opacity: 100;
            }
            &:hover p{
                opacity: 100;
            }
        }
    }
    &__secondText{
        padding-top: 180px;
        display: flex;
        justify-content: end;
        padding-right: 72px;
        padding-bottom: 260px;
        @include windowMax1440p {
            padding-top: 140px;
            padding-right: 0px;
            padding-bottom: 180px;  
        }
        @include windowMax1280p{
            padding-top: 120px;
            padding-bottom: 160px;
        }
        @include mobileMax952p{
            padding-top: 72px;
            padding-right: 0px;
            padding-bottom: 96px
        }
        &__wrap{
            p{
                @include poppins16_300();
                letter-spacing: 0.04em;
                max-width: 910px;
                color: var(--space-cadet-color);
                @include windowMax1440p {
                    @include poppins14_300();
                    max-width: 774px;   
                }
                @include windowMax1280p{
                    @include poppins12_300();
                    max-width: 681px;
                }
                @include mobileMax952p{
                    @include poppins14_300();
                }
            }
            &__button{
                width: fit-content;
                cursor: pointer;
                display: flex;
                align-items: baseline;
                p{
                    @include poppins14_500();
                    letter-spacing: 0.1em;
                    padding-top: 48px;
                    padding-right: 16px;
                    text-transform: uppercase;
                    color: var(--iris-color);
                    @include windowMax1440p{
                        padding-top: 32px;
                        @include poppins12_500();
                    }
                    @include mobileMax952p{
                        padding-top: 32px;
                        @include poppins12_500();
                    }
                    &::after{
                        content: "";
                        height: 2px;
                        background-color: var(--iris-color);
                        width: 0px;
                        display: inherit;
                        transition: width 0.3s ease-out;
                    }
                    &:hover{
                        &::after{
                            width: 100%;
                        }
                    }
                }
                &__circle{
                    background-color: var(--iris-color);
                    transition: background-color 0.3s ease-out;
                    width: 28px;
                    height: 28px;
                    border-radius: 60px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                &:hover &__circle{
                    background-color: var(--iris-color-hover);
                }
                
            }
        }
    }
}